	module.exports = {"data":{"post":{"html":"<p>This week we're painting and laying flooring upstairs or, more correctly, having it done. However, the carpenter is behind, he's only half done, and is flying off on vacation next week. Currently all rooms upstairs are impassable, except (thank God!) the bathroom.</p>\n<p>The cleaning lady also comes on Thursday. (Notice how we outsource much of our work.) With the work upstairs today, she can only do the downstairs and the bathroom.</p>\n<p>Finally, Christopher woke up today with fever and a cough, so we kept him at home with me. We're staying downstairs with Christopher's Playmobil figures, the carpenter is upstairs, and the cleaning lady is pulling weeds outside. House cleaning is postponed until next week.</p>\n<p>Normally one would go to work to escape such chaos, but since I work from home, there is no escape, even if Christopher weren't sick.</p>","fields":{"title":"Chaos Management","date":"30 Jun 2005","url":"https://papascott.de/2005-06-30-chaos-management/"}}},"pathContext":{"slug":"/2005-06-30-chaos-management/","prev":{"fields":{"title":"The 10 commandments of system administration","date":"27 Jun 2005","slug":"/2005-06-27-the-10-commandments-of-system-administration/"}},"next":{"fields":{"title":"No Confidence","date":"01 Jul 2005","slug":"/2005-07-01-no-confidence/"}}}}

